/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */
.prova {
	border-left: 1px solid var(--wp--preset--color--light-blue);
}
.indice-wrapper {
	padding: 1rem;
}

.indice-bar {
	height: 4px;
	background-color: var(--wp--preset--color--gray-ultra-light);
	margin-bottom: 1rem;
	overflow: hidden;
	position: relative;
}

.indice-bar-fill {
	height: 100%;
	width: 0%;
	background-color: var(--wp--preset--color--light-blue);
	transition: width 0.5s ease-in-out;
}

.indice-list {
	list-style: none;
	padding-left: 0;
	margin: 0;
}

.indice-item {
	padding-left: 1rem;
	margin-bottom: 1rem;
	position: relative;
	color: var(--wp--preset--color--light-blue);
	font-size: 1.2rem;
}

.indice-item a {
	color: var(--wp--preset--color--light-blue);
}
.indice-item a:hover {
	color: var(--wp--preset--color--dark-blue);
}

.indice-item::before {
	content: "";
	position: absolute;
	left: 0;
	top: 0.25rem;
	height: 1.5rem;
	width: 2px;
	background-color: transparent;
}

.indice-item.active::before {
	background-color: var(--wp--preset--color--light-blue);
}

.indice-title {
	color: var(--wp--preset--color--light-blue);
	background-color: transparent !important;
}

.accordion-button.indice-title:not(.collapsed) {
	background-color: transparent !important;
	color: var(--wp--preset--color--light-blue);
	box-shadow: none;
}

.accordion-button.indice-title {
	border: none;
}
